@charset "UTF-8";
.filter-wrapper {
  background-color: #fff;
  border: 1px solid #c0c0c0;
  display: flex;
  flex-direction: column; /*لعرض العناصر تحت بعض */
}
.filter-wrapper .filter-title {
  background-color: #2a5ea2;
  padding: 10px;
  color: #fff;
  margin-bottom: 20px;
}
.filter-wrapper > div {
  padding: 5px 20px;
  padding-bottom: 13px; /*كل عنصر يفصل عن اللي بعده*/
}
.filter-wrapper select {
  display: block;
  height: 40px;
  border: 1px solid #ddd;
  width: 100%;
  border-radius: 5px;
}